import './index.scss';

import classNames from 'classnames';
import { InputHTMLAttributes, memo, useEffect } from 'react';
import { Controller, useFormContext } from 'react-hook-form';

// ----------------------------------------------------------------------

export type RadioVarinat = 'default' | 'button' | 'segment';

export type RadioProps = {
  name: string;
  variant?: RadioVarinat;
  size?: 'lg' | 'md' | 'sm' | 'xs';
  color?: 'primary' | 'secondary';
  fullWidth?: boolean;
  value?: string | number;
  label?: string;
  id?: string;
  reverse?: boolean;
  hasContext?: boolean;
  defaultValue?: string | number;
} & Omit<InputHTMLAttributes<HTMLInputElement>, 'size'>;

const VARIANT_MAPS = {
  default: 'qb-radio--default',
  button: 'qb-radio--button',
  segment: 'qb-radio--segment'
};

const SIZE_MAPS = {
  lg: 'qb-radio--lg',
  md: 'qb-radio--md',
  sm: 'qb-radio--sm',
  xs: 'qb-radio--xs'
};

const COLOR_MAPS = {
  primary: 'qb-radio--primary',
  secondary: 'qb-radio--secondary'
};

export const ContextRadio = ({
  name,
  label,
  id,
  variant = 'default',
  size = 'md',
  color = 'primary',
  fullWidth = false,
  reverse = false,
  disabled,
  defaultValue,
  ...props
}: RadioProps) => {
  const { control, setValue } = useFormContext();

  useEffect(() => {
    if (defaultValue) {
      setValue(name, defaultValue);
    }
  }, [name, defaultValue, setValue]);

  return (
    <Controller
      name={name}
      control={control}
      render={({ field: { ref, onChange, ...fieldRest } }) => (
        <div
          className={classNames(
            'qb-radio',
            VARIANT_MAPS[variant],
            SIZE_MAPS[size],
            COLOR_MAPS[color],
            {
              'qb-radio--reverse': reverse
            },
            {
              'qb-radio--disabled': disabled
            },
            { 'qb-radio--full-width': fullWidth }
          )}
        >
          <input
            type="radio"
            ref={ref}
            id={id}
            {...fieldRest}
            defaultValue={defaultValue}
            value={props.value}
            {...props}
            disabled={disabled}
            onChange={onChange}
            className="qb-radio__form"
          />
          {variant === 'default' && <i className="qb-radio__icon" />}
          {label && (
            <label htmlFor={id} className="qb-radio__label">
              {label}
            </label>
          )}
        </div>
      )}
    />
  );
};

const Radio = ({
  variant = 'default',
  name,
  label,
  id,
  size = 'md',
  color = 'primary',
  fullWidth = false,
  reverse = false,
  hasContext = false,
  defaultValue,
  disabled,
  ...props
}: RadioProps) => {
  return (
    <>
      {hasContext ? (
        <ContextRadio
          variant={variant}
          name={name}
          size={size}
          color={color}
          label={label}
          id={id}
          reverse={reverse}
          disabled={disabled}
          defaultValue={defaultValue}
          {...props}
        />
      ) : (
        <div
          className={classNames(
            'qb-radio',
            SIZE_MAPS[size],
            COLOR_MAPS[color],
            VARIANT_MAPS[variant],
            {
              'qb-radio--reverse': reverse
            },
            {
              'qb-radio--disabled': disabled
            },
            { 'qb-radio--full-width': fullWidth }
          )}
        >
          <input
            type="radio"
            name={name}
            id={id}
            disabled={disabled}
            className="qb-radio__form"
            {...props}
          />
          {variant === 'default' && <i className="qb-radio__icon" />}
          {label && (
            <label htmlFor={id} className="qb-radio__label">
              {label}
            </label>
          )}
        </div>
      )}
    </>
  );
};

const MemoizedRadio = memo(Radio);
export default MemoizedRadio;
