import './index.scss';

import classNames from 'classnames';
import Label from 'components/forms/Label';
import Radio from 'components/forms/Radio';
import { ChangeEvent, Fragment, ReactNode, useId } from 'react';

import { ContextRadio, RadioProps } from '../Radio';

import type { RadioVarinat } from 'components/forms/Radio';

// ----------------------------------------------------------------------

export type RadioGroupProps = {
  direction?: 'flex-row' | 'flex-col' | 'grid-row' | 'grid-col';
  variant?: RadioVarinat;
  fullWidth?: boolean;
  name: string;
  label?: ReactNode;
  required?: boolean;
  items?: 1 | 2 | 3 | 4 | 5;
  gap?: string;
  options: Pick<RadioProps, 'value' | 'label' | 'disabled'>[];
  onChange?: (e: ChangeEvent<HTMLInputElement>) => void;
  defaultValue?: string | number;
  hasContext?: boolean;
};

const ITEM_MAPS = {
  1: 'radio-group--items-1',
  2: 'radio-group--items-2',
  3: 'radio-group--items-3',
  4: 'radio-group--items-4',
  5: 'radio-group--items-5'
};

const RadioGroup = ({
  direction = 'flex-row',
  variant = 'default',
  fullWidth = false,
  name,
  label,
  required = false,
  items = 1,
  gap,
  options,
  onChange,
  defaultValue,
  hasContext = false
}: RadioGroupProps) => {
  const id = useId();

  const isGridType = direction.includes('grid');
  const itemsClass = isGridType ? ITEM_MAPS[items] || '' : '';

  const renderOptions = () => {
    return options.map(
      ({ value, label, disabled }: Pick<RadioProps, 'value' | 'label' | 'disabled'>) => {
        const shortenedOptionLabel = value?.toString().replace(/\s+/g, '');
        const optionId = `${id}-${shortenedOptionLabel}`;
        const isDefaultChecked = defaultValue === value;

        return (
          <Fragment key={optionId}>
            {hasContext ? (
              <ContextRadio
                variant={variant}
                fullWidth={fullWidth}
                id={optionId}
                value={value}
                label={label}
                name={name}
                disabled={disabled}
                onChange={onChange}
                defaultValue={defaultValue}
                defaultChecked={isDefaultChecked}
              />
            ) : (
              <Radio
                variant={variant}
                fullWidth={fullWidth}
                id={optionId}
                value={value}
                label={label}
                name={name}
                disabled={disabled}
                onChange={onChange}
                defaultChecked={isDefaultChecked}
              />
            )}
          </Fragment>
        );
      }
    );
  };

  return (
    <div
      className={classNames(
        'radio-group',
        {
          'radio-group--flex-row': direction === 'flex-row',
          'radio-group--flex-column': direction === 'flex-col',
          'radio-group--grid-row': direction === 'grid-row',
          'radio-group--grid-column': direction === 'grid-col'
        },
        itemsClass,
        { 'radio-group--full-width': fullWidth },
        { 'radio-group--segment': variant === 'segment' }
      )}
    >
      {label && <Label text={label} required={required} />}
      <div className={classNames('radio-group__body', gap)}>{renderOptions()}</div>
    </div>
  );
};

export default RadioGroup;
